<template>
  <div style="wdith:100vw;height:100vh;">
    <!-- 一级导航栏 -->
    <!-- margin-left:50%;transform:translate(-10%, 0); -->
    <div style="width:100vw;height:60px;background:#545c64">
      <el-menu 
        :default-active="activeIndex" 
        class="el-menu-demo" 
        style="width:450px;margin-left:50%;transform:translate(-50%, 0);"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        mode="horizontal" 
        @select="handleFirLevelSelect">
        <el-menu-item index="1" style="margin-right:30px;">病人数据上传</el-menu-item>
        <el-menu-item index="2" style="margin-right:30px;">手动输入数据</el-menu-item>
        <el-menu-item index="3">结果展示</el-menu-item>
      </el-menu>
    </div>
    <div style="width:100vw;height:calc(100vh - 60px);background:white;">
      <div v-if="activeIndex === '1'" style="margin-left:50%;transform:translate(-35%, 30%);">
        <el-upload
          class="upload-demo upload-custom-style"
          action="/api/aki/"
          :on-preview="handlePreview"
          :on-success="uploadSuccess"
          :file-list="fileList"
          :on-error="uploadError">
          <el-button size="small" type="primary" style="width:200px;font-size:20px;">上传病人数据文件</el-button>
          <div slot="tip" class="el-upload__tip" style="font-size:18px;">=只能上传xlsx/xls文件=</div>
        </el-upload> 
      </div>
      <div v-if="activeIndex === '2'">
        <div style="margin-left:50%;transform:translate(-100%, 50px);">
          <el-form ref="form" :model="form" label-width="200px">
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="患者编号">
                <el-input v-model="form.pid" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="年龄">
                <el-input v-model="form.age" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="白蛋白">
                <el-input v-model="form.alb" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="急性肝损伤">
                <el-input v-model="form.ali" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="谷丙转氨酶">
                <el-input v-model="form.alt" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="谷草转氨酶">
                <el-input v-model="form.ast" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="性别">
                <el-select v-model="form.region" placeholder="请选择" style="width:100px;">
                  <el-option label="男性" value="man"></el-option>
                  <el-option label="女性" value="woman"></el-option>
                </el-select>
              </el-form-item> 
              <el-form-item label="ICU住院时长">
                <el-input v-model="form.los_icu" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="白细胞计数">
                <el-input v-model="form.wbc" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血小板计数">
                <el-input v-model="form.plt" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="中性粒细胞计数">
                <el-input v-model="form.neut" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="淋巴细胞计数">
                <el-input v-model="form.ly" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="总胆红素">
                <el-input v-model="form.tbil" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血清肌酐">
                <el-input v-model="form.scr" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="血尿素氮">
                <el-input v-model="form.bun" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="红细胞计数">
                <el-input v-model="form.rbc" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="血红蛋白浓度">
                <el-input v-model="form.hbg" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="红细胞压积">
                <el-input v-model="form.hct" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="血浆">
                <el-input v-model="form.plasma" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="糖尿病">
                <el-input v-model="form.diabetes" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="活性化部分凝血激素时间">
                <el-input v-model="form.ptt" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="凝血脢原时间">
                <el-input v-model="form.pt" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="国际标准化比值">
                <el-input v-model="form.inr" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="机械通风">
                <el-input v-model="form.mv" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="中性粒/淋巴">
                <el-input v-model="form.nlr" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血小板/淋巴">
                <el-input v-model="form.plr" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="sapsii指数">
                <el-input v-model="form.sap" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="体重">
                <el-input v-model="form.weight" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="身高">
                <el-input v-model="form.height" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="高血压">
                <el-input v-model="form.hypertension" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="二氧化碳分压">
                <el-input v-model="form.pco2" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="氧分压">
                <el-input v-model="form.po2" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="样本">
                <el-input v-model="form.specimen" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="体温">
                <el-input v-model="form.tempc" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="呼吸指数">
                <el-input v-model="form.resprate" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="平均动脉压">
                <el-input v-model="form.meanbp" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="心率">
                <el-input v-model="form.heartrate" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血糖">
                <el-input v-model="form.glucose" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="血氧饱和度">
                <el-input v-model="form.spo2" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="舒张压">
                <el-input v-model="form.diasbp" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="血清二氧化碳结合力">
                <el-input v-model="form.co2" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血清氯">
                <el-input v-model="form.cl" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="乳酸">
                <el-input v-model="form.lactate" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="血清钠">
                <el-input v-model="form.sodium" style="width:100px;"></el-input>
              </el-form-item>
            </div>

            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="格拉斯哥昏迷评分法">
                <el-input v-model="form.mingcs" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="BMI指数">
                <el-input v-model="form.bmi" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="心脏相关疾病">
                <el-input v-model="form.heart" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="肺部疾病">
                <el-input v-model="form.lung" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            
            <div style="display:flex;justify-content:space-start;">
              <el-form-item label="精神疾病">
                <el-input v-model="form.neuro" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="没有共并症">
                <el-input v-model="form.noCormobidity" style="width:100px;"></el-input>
              </el-form-item>
              <el-form-item label="是否有两种共并症">
                <el-input v-model="form.has2diseases" style="width:100px;"></el-input>
              </el-form-item> 
              <el-form-item label="是否有三种共并症">
                <el-input v-model="form.more3diseases" style="width:100px;"></el-input>
              </el-form-item>
            </div>
            <el-button type="primary" @click="showCenterDialogVisible" round style="width:300px;margin-left:50%;transform:translate(50%, 0);">提交数据</el-button>
          </el-form>
        </div>
      </div>  
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="300px"
        center>
        <span style="margin-left:35%;">确认提交吗？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitData">确 定</el-button>
        </span>
      </el-dialog>    
      <!-- 结果展示 -->
      <div v-if="activeIndex === '3'">
        <div style="font-size:23px;font-weight:500;text-align:center;margin:20px 0;">风险等级：<label style="color:red;">危重(75～100%)</label> ｜ <label style="color:#f2b611;">严重(50%~75%)</label> ｜ <label style="color:blue;">较严重(25%~50%)</label> ｜ <label style="color:green;">较轻(0~25%)</label></div>
        <el-table
          :data="listData"
          stripe
          border 
          style="margin-bottom:14px;margin-left:50%;transform:translate(-600px, 0);">
          <el-table-column align="center" property="user_id" label="编号" width="100"></el-table-column>
          <el-table-column align="center" property="gender" label="性别" width="100"></el-table-column>
          <el-table-column align="center" property="me_ve" label="是否机械通风" width="200"></el-table-column>
          <el-table-column align="center" property="proba" label="院内死亡概率(预测)" width="200">
            <template slot-scope="scope">
          <!-- 意思是：给这个div绑定动态样式，颜色color的属性值为getColorByNation()这个方法的返回值，所以只需要通过传过去的scope 对方法的返回值做动态设置即可  -->
            <div :style="{background:getBackgroundByProba(scope), fontSize:'20px', fontWeight:600}">{{scope.row.proba}}</div>
          </template>
          </el-table-column>
          <el-table-column align="center" property="treat_advice" label="治疗建议" width="300">
            <template slot-scope="scope">
              <span v-if="scope.row.treat_advice_null">
                <el-input size="small" v-model="scope.row.treat_advice" @blur="handleInputBlur(scope.row)"></el-input>
              </span>
              <span v-else>{{scope.row.treat_advice}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" property="nurse_advice" label="护理建议" width="300">
            <template slot-scope="scope">
              <span v-if="scope.row.nurse_advice_null">
                <el-input size="small" v-model="scope.row.nurse_advice" @blur="handleInputBlur(scope.row)"></el-input>
              </span>
              <span v-else>{{scope.row.nurse_advice}}</span>
            </template>
          </el-table-column>
        </el-table>
        <el-image
          v-if="imgUrl"
          style="width:600px;height:450px;margin-left:50%;transform:translate(-300px, 0);"
          :src="imgUrl">
        </el-image>
      </div>
    </div>    
  </div>
</template>

<script>
import {getInfo} from '../axios/api.js'
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      patientId: '0',
      form: {},
      fileList: [],
      fileId: 0,
      imgUrl: '',
      activeIndex: '1',
      activeName: 'first',
      centerDialogVisible: false,

      // table
      total1: 0,
      currentPage1: 1,
      pageSize: 10,
      listData: []
    }
  },
  created: async function () {
    this.listData = []
    this.patiendId = -1
    this.fileId = -1
  },
  methods: {

    // 表格点击事件
    handleInputBlur (row) {
      if (row.treat_advice && row.treat_advice_null) {
        let params = {
          'id': row.id,
          'treat_advice': row.treat_advice
        }
        this.$http.post('/api/aki/update_statistic/', params).then(res => {
          if (res.status === 200) {
            row.treat_advice_null = false
            let tmpListData = this.listData
            for(let i in tmpListData) {
              if (tmpListData[i].id === row.id) {
                tmpListData[i] = row
                break
              }
            }
            this.listData = tmpListData
          }
        })
      }
      if (row.nurse_advice && row.nurse_advice_null) {
        let params = {
          'id': row.id,
          'nurse_advice': row.nurse_advice
        }
        this.$http.post('/api/aki/update_statistic/', params).then(res => {
          if (res.status === 200) {
            row.nurse_advice_null = false
            let tmpListData = this.listData
            for(let i in tmpListData) {
              if (tmpListData[i].id === row.id) {
                tmpListData[i] = row
                break
              }
            }
            this.listData = tmpListData
          }
        })
      }
    },

    submitData () {
      this.fileId = 0
      this.centerDialogVisible = false
      this.patiendId = Math.random().toString(36).slice(-8)
      let params = {
        'li_data': this.pkgPostData(),
        'patient_id': this.patientId
      }
      this.$http.post('/api/aki/', params).then(res => {
      })
    },

    showCenterDialogVisible () {
      this.centerDialogVisible = true
    },

    // 文件上传相关
    handlePreview(file) {
      
    },
    handleUpOrInput(tab, event) {
      this.activeName = tab.name
    },
    handleFirLevelSelect (key, keyPath) {
      this.activeIndex = String(key)
      if (key === '3') {
        this.$http.get("/api/aki/?file_id="+this.fileId+'&patient_id='+this.patientId).then((res) => {
          let gender = res.data.patients[0].gender
          if (res.status === 200) {
            let tmpListData = []
            res.data.patients.forEach((item, index) => {
              if (item.treat_advice === '') {
                item.treat_advice_null = true
              } else {
                item.treat_advice_null = false
              }
              if (item.nurse_advice === '') {
                item.nurse_advice_null = true
              } else {
                item.nurse_advice_null = false
              }
              tmpListData.push(item)
            })
            this.imgUrl = res.data.img_url
            this.listData = tmpListData
          }
          // 接口调用成功回调
        }).catch((error) => {
          // 接口调用失败毁掉
        });
      }
    },
    uploadSuccess (res, file, filelist) {
      this.fileList = []
      this.fileId = res.file_id
      this.patientId = '0'
      this.$message({
          showClose: true,
          message: '数据提交成功！',
          type: 'success'
        });
    },
    uploadError (err, file, filelist) {
      this.imgUrl = ''
      this.$message.error('文件格式 / 文件内容出错，请检查！')
    },
    pkgPostData () {
      let data = []
      data.push(this.form.pid || 0)
      data.push(this.form.age || 0)
      data.push(this.form.alb || 0)
      data.push(this.form.ali || 0)
      data.push(this.form.alt || 0)
      data.push(this.form.ast || 0)
      if (this.form.gender === '男性') {
        data.push('1')
      } else {
        data.push ('0')
      }
      data.push(this.form.los_icu || 0)
      data.push(this.form.wbc || 0)
      data.push(this.form.plt || 0)
      data.push(this.form.neut || 0)
      data.push(this.form.ly || 0)
      data.push(this.form.tbil || 0)
      data.push(this.form.scr || 0)
      data.push(this.form.bun || 0)
      data.push(this.form.rbc || 0)
      data.push(this.form.hbg || 0)
      data.push(this.form.hct || 0)
      data.push(this.form.ptt || 0)
      data.push(this.form.pt || 0)
      data.push(this.form.inr || 0)
      data.push(this.form.mv || 0)
      data.push(this.form.nlr || 0)
      data.push(this.form.plr || 0)
      data.push(this.form.sap || 0)
      data.push(this.form.weight || 0)
      data.push(this.form.height || 0)
      data.push(this.form.hypertension || 0)
      data.push(this.form.pco2 || 0)
      data.push(this.form.po2 || 0)
      data.push(this.form.specimen || 0)
      data.push(this.form.tempc || 0)
      data.push(this.form.resprate || 0)
      data.push(this.form.meanbp || 0)
      data.push(this.form.heartrate || 0)
      data.push(this.form.glucose || 0)
      data.push(this.form.spo2 || 0)
      data.push(this.form.diasbp || 0)
      data.push(this.form.co2 || 0)
      data.push(this.form.cl || 0)
      data.push(this.form.lactate || 0)
      data.push(this.form.sodium || 0)
      data.push(this.form.mingcs || 0)
      data.push(0.09)
      data.push(this.form.bmi || 0)
      data.push(this.form.plasma || 0)
      data.push(this.form.diabetes || 0)
      data.push(this.form.heart || 0)
      data.push(this.form.lung || 0)
      data.push(this.form.neuro || 0)
      data.push(this.form.noCormobidity || 0)
      data.push(this.form.has2diseases || 0)
      data.push(this.form.more3diseases || 0)
      return data
    },
    getBackgroundByProba(scope){
      let proba = parseInt(scope.row.proba.split('%')[0])
      if (proba >= 75) {
        return 'red'
      } else if (50 <= proba && proba < 75) {
        return '#f2b611'
      } else if (25 <= proba && proba < 50) {
        return 'blue'
      } else {
        return 'green'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.upload-custom-style {
  margin-left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height:90px;
  margin-top: 100px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



